/** Scaffolding **/
#header {
  grid-row: 1;
  grid-column: 1 / 3;

  display: grid;
  grid-template-columns: 1fr auto;
}

#header-info {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

#header-controls {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -ms-flex-pack: end;
      justify-content: flex-end;
}



